*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #666;
  line-height: 1; }

.container {
  display: flex;
  flex-direction: column;
  width: 200px; }
  .container .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 30px;
    padding: 0 10px;
    background: #f7f7f7;
    border-bottom: 1px solid #e6e6e6; }
    .container .header .name,
    .container .header .feedback {
      cursor: pointer; }
  .container .main .donate {
    opacity: 0.5;
    max-width: 100%;
    transition: all 0.2s ease; }
    .container .main .donate:hover {
      opacity: 1; }
  .container .footer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    cursor: pointer;
    color: #23ade5;
    background: #f7f7f7;
    border-top: 1px solid #e6e6e6; }
